<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix header-box">
        <span>文章分类</span>
        <el-button type="primary" size="mini" @click="addCateBtnFn" >添加分类</el-button>
      </div>
      <el-table style="width: 100%" :data="cateList" border stripe>
        <el-table-column label="序号" width="100" type="index"></el-table-column>
        <el-table-column label="分类名称" prop="cate_name"></el-table-column>
        <el-table-column label="分类别名" prop="cate_alias"></el-table-column>
        <el-table-column label="操作">
          <template v-slot="scope">
            <el-button type="primary" size="mini" @click="updateCateFn(scope.row)">修改</el-button>
            <el-button type="danger" size="mini" @click="deleteCateFn(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 添加分类的对话框 -->
    <el-dialog :title="isEdit ? '编辑文章分类' : '添加文章分类'" :visible.sync="addVisible" width="35%" @closed="onAddClosedFn">
        <!-- 添加的表单 -->
      <el-form :model="addForm" :rules="addRules" ref="addRef" label-width="80px">
        <el-form-item label="分类名称" prop="cate_name">
          <el-input v-model="addForm.cate_name" minlength="1" maxlength="10"></el-input>
        </el-form-item>
        <el-form-item label="分类别名" prop="cate_alias">
          <el-input v-model="addForm.cate_alias" minlength="1" maxlength="15"></el-input>
        </el-form-item>
      </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button size="mini" @click="cancelFn">取 消</el-button>
            <el-button size="mini" type="primary"  @click="addCateFn">添 加</el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
import {getArtCateListAPI,addArtCateAPI,updateArtCateAPI,delArtCateAPI} from '@/api/index'
export default {
  name: 'ArtCate',
  data() {
    return {
      addRules: { // 添加表单的验证规则对象
        cate_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' },
          { pattern: /^\S{1,10}$/, message: '分类名必须是1-10位的非空字符', trigger: 'blur' }
        ],
        cate_alias: [
          { required: true, message: '请输入分类别名', trigger: 'blur' },
          { pattern: /^[a-zA-Z0-9]{1,15}$/, message: '分类别名必须是1-15位的字母数字', trigger: 'blur' }
        ]
    },
      cateList:[],
      addVisible:false,
      addForm:{
        cate_name: '',
        cate_alias: ''
      },
      editId: '', // 要修改的文章id
      isEdit: false, // 保存编辑状态(false新增, true编辑)
    }
  },  
  created(){
    this.getArtCateFn()
  },
  methods:{
    // 添加分类->点击出对话框
    addCateBtnFn () {
        this.addVisible = true // 让对话框出现
        this.editId = '' // 编辑的文章分类id设置无
        this.isEdit = false // 编辑的状态关闭
    },
    async getArtCateFn(){
      const res = await getArtCateListAPI()
      if(res.data.code!==0) return this.$message.error(res.data.message)
      this.cateList = res.data.data
    },
    // 取消关闭窗口事件
    cancelFn(){
      this.addVisible = false
      this.$refs.addRef.resetFields()
    },
    // 添加-按钮点击事件
    async addCateFn(){
      this.$refs.addRef.validate(async valid=>{
        if(valid){
          if(this.isEdit){
            // console.log(this.editId)
            this.addForm.id = this.editId
            console.log(this.editId)
            const res = await updateArtCateAPI(this.addForm)
            if(res.data.code!==0)return this.$message.error(res.data.message)
            this.$message.success('更新分类成功！')
          }else{
            const res = await addArtCateAPI(this.addForm)
            if(res.data.code!==0)this.$message.error(res.data.message)
            this.$message.success('添加分类成功！')
          }
          // 操作成功重新请求数据列表
          this.getArtCateFn()
          // 关闭对话框
          this.addVisible = false
        }else{
          return false
        }
      })
    },
    // 窗口关闭事件
    onAddClosedFn() {
      this.$refs.addRef.resetFields()
    },
    // 修改-按钮点击事件
    updateCateFn(obj){
      // console.log(obj)
      this.editId = obj.id // 获取编辑的文章分类id
      this.addVisible = true // 让对话框出现
      this.isEdit = true // 编辑的状态关闭
      // console.log(this.isEdit)

      // 设置数据回显
      /** resetFields() 对整个表单进行重置，将所有字段值重置为初始值并移除校验结果
       * 这里有一个关于resetFields()的bug
       * el-from配合el-dialog做数据回显时
       * dialog内容是懒渲染的，第一次被打开之前传入默认slot不会被渲染到DOM上，第二次知识做后续的css显隐
       * 所以第一次绑定的值必须是空的
       * 解决方法：nextTick() 
       * 作用:在下一次DOM更新结束后执行其指定的回调
       * 当改变数据后，要基于更新后的新DOM进行某些操作时
       * 此处：让DOM加载完成后先绑定空值 
       */
      this.$nextTick(() => {
          // 先让对话框出现, 它绑定空值的对象, 才能resetFields清空用初始空值
          this.addForm.cate_name = obj.cate_name
          this.addForm.cate_alias = obj.cate_alias
      })
    },
    // 删除-按钮点击事件
    async deleteCateFn(obj){
      const res = await delArtCateAPI(obj.id)
      if(res.data.code!==0) return this.$message.error(res.data.error.message)
      this.$message.success('删除分类成功！')
      this.getArtCateFn()
    },
  },
}
</script>

<style lang="less" scoped>
.header-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>